.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  border-radius: 50%;
}
main{
  max-width: 100vw;
  display: flex;
  font-size: .8em;
  
}
main > div{
  display:block;
  margin-left:5%;
  margin-right: 3%;
  left:10%;
}
.inscribed{
  position: relative;
  top: 150px;
  left:20%;
  transform: translate(-50%, -50%);
  object-fit: contain;

}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
    height:250px;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
